.backdrop {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.2);
    opacity: 1;
    transition: opacity 250ms cubic-bezier(0.4, 0, 0.2, 1);
    z-index: 999;
    
    overflow-y: scroll;
    
    &.is-hidden,
    &.is-hidden-contact {
        opacity: 0;
        pointer-events: none;
    }
}